@import url("https://fonts.googleapis.com/css2?family=Work+Sans:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Archivo:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
.bCardContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: min-content;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  background-color: white;
  width: fit-content;
}

.bCardContainer:hover {
  box-shadow: 0px 0px 10px 2px #0000001f;
  border-radius: 10px;
  background-color: white;
}
.bImageContainer {
  height: 180px;
  max-width: 230px;
}

.bTextContainer {
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Work Sans", sans-serif;
}

.bTitle {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  color: rgb(8, 8, 8);
  text-align: center;
  margin-top: 10px;
  font-weight: 600;
}

.bBrand {
  font-family: "Archivo", sans-serif;
  color: rgb(80, 80, 80);
}

.bPrice {
  font-family: "Archivo", sans-serif;
  margin-top: 5px;

  color: rgb(63, 146, 43);
  font-weight: bolder;
  margin-bottom: 5px;
}

.Bdetails {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 140px;
}
