@import url("https://fonts.googleapis.com/css2?family=Work+Sans:wght@500&display=swap");

.cardContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: min-content;
  padding: 5px;
  margin: 5px;
  border-radius: 10px;
  background-color: white;
}

.cardContainer:hover {
  box-shadow: 0px 0px 10px 2px #0000001f;
  border-radius: 10px;
  background-color: white;
}
.imageContainer {
  width: 70px;
  height: 50px;
}

.textContainer {
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Work Sans", sans-serif;
}
