    }
    .slide{
        display: flex
    
    }
    .searchButton{
        display: flex;
        background-color:rgba(0,0,0,0);
        border: none;
        margin-left: 20px;

    }

    .noneForPc{
        display: none;
    }
  }

  /* mobile screen */
  @media only screen and (max-width: 720px) {
    .navigation{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    
    }
    .header{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .logoAndButton{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }
    .slide{
        display: none
    }
    .searchButton{
        display: none
    }
    .navButton{
        background-color:rgba(0,0,0,0);
