::-webkit-scrollbar {
  height: 3px;
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 0px rgba(128, 128, 128, 0); 
  border-radius: 5px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(77, 76, 76, 0.753); 
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #3d3d3d;

}

/* mobile screen */
@media only screen and (max-width: 720px) {
 
  .bigHeading{
   font-size: 32px; 
