@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.productContainer {
  width: 100%;
  height: max-content;
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 30px;
}
.ProductDetail {
  padding: 26px;
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.actualPrice {
  color: rgb(114, 114, 114);
  padding: 5px;
  width: max-content;
  margin-top: 3px;
  text-decoration: line-through;
  margin-left: 15px;
}
.buttonSection {
  display: flex;
  justify-items: center;
}
.buyButton {
  padding: 5px;
  width: max-content;
  font-size: 20px;
  color: green;
}

.specs {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}

table {
  margin-top: 30px;
  margin-bottom: 30px;

  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}
td,
th,
td {
  border: 1px solid #dddddd;
  text-align: center;
  padding: 8px;
}

.ProductImage {
  display: flex;
  flex-direction: row;
}

.thumb {
  width: 60px;
  display: flex;
  flex-direction: column;
  margin: 5px;
  justify-content: space-between;
}

.thumb > img {
  border: 2px solid #3f3f3f;
  padding: 3px;
}

.preview > img {
  max-height: 256px;
  width: 100%;
}

/* mobile screen */
@media only screen and (max-width: 720px) {
  .ProductImage {
    width: 100%;
  }
}
